header{
  /* max-width:100vw; */
  height:100vh;
  display: flex;
  flex-flow: column;
  padding-left:6vmin;
  align-items: left;
  justify-content: center;
  overflow: hidden;
}

.jumbotron h1 {
  margin:-1vmin;
}
.landingPageContent h1 {
    margin:-1vmin;
}

.jumbotronHoriz{
  font-family: 'Barlow', sans-serif;
  font-weight: 600;
  font-size: 13vmin;
  line-height: 14vmin;
  letter-spacing: -.66vmin;
  color: #FFFFFF;
}

.deEmphasized{
  color:#888888;
}

.landingPageContent{
  display: flex;
  flex-flow: column;
}

section {
  /* width:100vw; */
  height:100vh;
  display: flex;
  flex-flow: column;
  padding-left:2rem;
  align-items: left;
}

footer{
  margin-top:20vh;
  height:100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

footer .textRow {
  margin-top: 10vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-decoration: none;
}
footer .textRow a {
  padding: 0 3vw;
  color:white;
  opacity: .6;
  font-family: 'Barlow', sans-serif;
  font-size: 3vmin;
  font-weight: 600;
}

footer .textRow a:hover {
  padding: 0 3vw;
  color:aqua;
  opacity: 1;
  font-family: 'Barlow', sans-serif;
  font-size: 2.88vmin;
  font-weight: 600;
}

.happyPic{
  width:180px;
  height:225px;;
  background-color: pink;
  background-image: url(./img/newish.png);
  margin-bottom:5vmin;
}
.happyPic:hover {
  background-image: url(./img/yung.png);
}
