/* typography */
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@300;600&display=swap');

h1 {
  font-family: 'Barlow', sans-serif;
  font-weight: 600;
  font-size: 13vmin;
  line-height: 14vmin;
  letter-spacing: -.44vmin;
  color: #FFFFFF;
}

h2 {
  font-family: 'Barlow', sans-serif;
  font-weight: 600;
  font-size: 6em;
  letter-spacing: -0.03em;
  color: #FFFFFF;
}

h3 {
  font-size: 1.33em;
}

h5 {
  font-family: 'Barlow', sans-serif;
  font-size: 32px;
  font-weight: 300;
  line-height: 27px;
  letter-spacing: 0.1em;
	text-transform: uppercase;
  text-align: left;
	color:white;
	opacity:.6;
}

pageSubheader {
  line-height: normal;
}
